import React, { useState } from "react";
import styled from "@emotion/styled";
import logo from "../Components/tesla-logo.svg";
import MenuIcon from "@mui/icons-material/Menu";
import CloseIcon from "@mui/icons-material/Close";
import { selectCars } from "../features/car/carSlice";
import { useSelector } from "react-redux";

const Header = () => {
  const [closeStatus, setCloseStatus] = useState(false);
  const cars = useSelector(selectCars);

  return (
    <>
      <Container>
        <LeftMenu>
          <a href="#">
            <img src={logo} height="16px" width="120px" alt="Tesla" />
          </a>
        </LeftMenu>
        <Menu>
          {cars &&
            cars.map((car, index) => {
              return (
                <a key={index} href="#">
                  {car}
                </a>
              );
            })}

          <a href="#">Solar Roof</a>
          <a href="#">Solar Panels</a>
        </Menu>
        <RightMenu>
          <a href="#">Shop</a>
          <a href="#">Tesla Account</a>
          <CustomMenu onClick={() => setCloseStatus(true)}></CustomMenu>
        </RightMenu>
        <BurgerNav show={closeStatus}>
          <CloseWrapper>
            <CustomClose onClick={() => setCloseStatus(false)} />
          </CloseWrapper>
          <li>
            <a href="#">Model S</a>
          </li>
          <li>
            <a href="#">Model 3</a>
          </li>
          <li>
            <a href="#">Model X</a>
          </li>
          <li>
            <a href="#">Model Y </a>
          </li>
          <li>
            <a href="#">Solar Roofs</a>
          </li>
          <li>
            <a href="#">Solar Panels</a>
          </li>
          <li>
            <a href="#">Existing Inventory</a>
          </li>
          <li>
            <a href="#">Used Inventory</a>
          </li>
          <li>
            <a href="#">Test Drive</a>
          </li>
          <li>
            <a href="#">Trade In</a>
          </li>
          <li>
            <a href="#">Powershell</a>
          </li>
        </BurgerNav>
      </Container>
    </>
  );
};

export default Header;

const Container = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  min-height: 60px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
`;
const LeftMenu = styled.div`
  a {
    position: relative;
    left: 32px;
  }
`;
const Menu = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  

  a {
    text-decoration: none;
    flex-wrap: nowrap;
    padding: 0 10px;
    text-transform: capitalize;
    @media (max-width: 900px) {
    display: none;
  }
  } a:hover {
  height: 35px;
  border-radius: 15px;
  background-color: #b2c1e2;
  transition: ease;
  
`;
const RightMenu = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  right: 32px;
  a {
    text-decoration: none;
    flex-wrap: nowrap;
    margin-right: 10px;
    padding: 0 10px;
    text-transform: capitalize;
    @media (max-width: 768px) {
      display: none;
    }

    a:hover {
      height: 35px;
      border-radius: 15px;
      background-color: #b2c1e2;
      transition: ease;
    }
  }
`;
const CustomMenu = styled(MenuIcon)`
  cursor: pointer;
`;
const BurgerNav = styled.div`
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: 300px;
  padding: 24px;
  z-index: 100;
  background: #fff;
  display: flex;
  flex-direction: column;

  li {
    list-style: none;
    line-height: 20px;
    font-size: 14px;
    padding: 4px 8px;
    margin-bottom: 8px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    a {
      color: #393c41;
      text-decoration: none;
    }
  }
  li:hover {
    border-radius: 10px;
    background-color: #e2ebf1;
    transition: ease;
  }
  transform: ${(props) => (props.show ? `translateX(0)` : `translateX(100%)`)};
  transiton: transform 0.9s ease;
`;
const CustomClose = styled(CloseIcon)`
  color: #393c41;
  cursor: pointer;
  font-size: 32px;
  CloseIcon:hover {
    background: red;
  }
`;
const CloseWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
`;
