import React from "react";
import Header from "./Components/Header";
import Section from "./Components/Section";
import Data from "./Components/Data";

function App() {
  return (
    <>
      <Header />
      {Data.map((val, indx) => {
        const { title, subtitle, image, leftButton, rightButton } = val;
        return (
          <Section
            key={indx}
            title={title}
            subtitle={subtitle}
            image={image}
            leftButton={leftButton}
            rightButton={rightButton}
          />
        );
      })}
      {/* <Footer/> */}
    </>
  );
}

export default App;
